@import "./utilities";

#about {

  #about-sub-section {
    @extend %flex-container-column;
    height: 90%;
    width: 90%;
    align-items: center;
  }

  .container {
    @extend %flex-container-column;
    align-items: center;
  }

  article {
    @extend %flex-container-column;
    padding: 2.5rem 0 0 0;
  }

  h3{
    padding: 1rem 0 0 0;
  }

  img {
    max-width: 100%;
    width: 40%;
    height: auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    align-self: center;
  }

  svg {
    font-size: 1.5em;
    color: $dark-grey;
  }

}
